.header {
  position: fixed;
  width: 100%;
  z-index: 100;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
  padding: 60px 0px;
  opacity: 0;
  padding-bottom: 0px;
  /*------------------*/
  /*--- ATTRIBUTES ---*/
  /*------------------*/
  /*----------------*/
  /*--- ELEMENTS ---*/
  /*----------------*/
  /*------------------*/
  /*--- RESPONSIVE ---*/
  /*------------------*/
}
.header.motion-in {
  opacity: 1;
}
.header.show-logo .logo .text {
  opacity: 1;
  transform: translateX(0px);
}
.header.h-white:before {
  background: #fff;
}
.header.h-white.show-menu .hmbrg:before,
.header.h-white.show-menu .hmbrg:after {
  background: #000000;
}
.header.h-white.show-menu .hmbrg .text {
  color: #000000;
}
.header.h-white .hmbrg:before,
.header.h-white .hmbrg:after {
  background: #fff;
}
.header.h-white .logo .ico path {
  fill: #fff;
}
.header.h-white .logo .text path {
  fill: #fff;
}
.header.h-white .menu {
  color: #141515;
}
.header.h-white .menu .legals:before {
  background: #141515;
}
.header.h-white .menu .social svg path {
  fill: #141515;
}
.header.show-menu:before {
  transform: scale(100);
}
.header.show-menu .hmbrg:before,
.header.show-menu .hmbrg:after {
  background: #fff;
}
.header.show-menu .hmbrg .text {
  color: #fff;
}
.header.show-menu .logo .ico path {
  fill: #FCD572;
}
.header.show-menu .logo .text path {
  fill: #fff;
}
.header:before {
  content: '';
  display: block;
  position: absolute;
  width: 50px;
  height: 50px;
  background: #141515;
  right: 50px;
  top: 43px;
  border-radius: 100%;
  transform: scale(0);
  transition: 0.7s all cubic-bezier(0.55, 0.085, 0, 0.99);
  z-index: -1;
}
.header .content {
  display: flex;
  width: 100%;
  justify-content: space-between;
  position: relative;
}
.header .logo {
  width: 126px;
  height: 56px;
  background: url('../img/logo.svg') no-repeat center center;
  background-size: contain;
  cursor: pointer;
  z-index: 2;
}
.header .logo:hover .text {
  opacity: 1;
  transform: translateX(0px);
}
.header .logo .ico path {
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
  fill: #FCD572;
}
.header .logo .text {
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
  opacity: 0;
  transform: translateX(20px);
}
.header .logo .text path {
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
  fill: #000000;
}
.header .hmbrg {
  position: relative;
  width: 30px;
  height: 15px;
  display: block;
  cursor: pointer;
  z-index: 2;
  margin-top: 7px;
}
.header .hmbrg:hover:before {
  width: 80%;
}
.header .hmbrg:hover:after {
  width: 60%;
}
.header .hmbrg.active .text {
  opacity: 1;
  left: -180%;
}
.header .hmbrg.active:before {
  width: 100%;
  transform: rotate(45deg) translate(5px, 4px);
}
.header .hmbrg.active:after {
  width: 100%;
  transform: rotate(-45deg) translate(5px, -4px);
}
.header .hmbrg .text {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  left: -150%;
  font-family: "Blacker Sans Pro Book";
  font-style: normal;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #000000;
  opacity: 0;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
}
.header .hmbrg:before,
.header .hmbrg:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  top: 0px;
  display: block;
  right: 0px;
  background: #000000;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
}
.header .hmbrg:after {
  bottom: 0px;
  top: auto;
  width: 80%;
}
@media (min-width: 3000px) {
  .header.show-menu:before {
    transform: scale(240);
  }
}
@media (min-width: 2000px) {
  .header.show-menu:before {
    transform: scale(130);
  }
}
@media (max-width: 1024px) {
  .header {
    height: 130px;
    padding: 40px 0px;
  }
  .header .logo {
    width: 96px;
    height: 46px;
  }
}
@media (max-width: 850px) {
  .header:before {
    right: 30px;
    top: 23px;
  }
}
@media (max-width: 430px) {
  .header {
    height: 36px;
    padding: 36px 0px;
  }
  .header .container-fluid {
    padding: 0px 30px;
  }
  .header .container-fluid .row {
    align-items: center;
    margin: 0px;
  }
  .header .container-fluid .row .col {
    padding: 0px;
    width: 100%;
  }
  .header:before {
    right: 0px;
    top: 20px;
  }
  .header .logo {
    width: 76px;
    height: 35px;
  }
  .header .hmbrg .text {
    font-size: 12px;
    left: -100%;
  }
  .header .hmbrg.active .text {
    left: -140%;
  }
}
