.loader {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 2000;
  top: 0px;
  left: 0px;
  display: none;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  /*------------------*/
  /*--- ATTRIBUTES ---*/
  /*------------------*/
  /*----------------*/
  /*--- ELEMENTS ---*/
  /*----------------*/
  /*------------------*/
  /*--- RESPONSIVE ---*/
  /*------------------*/
}
.loader.show {
  display: flex;
}
.loader.motion-in .logo {
  opacity: 1;
}
.loader.motion-in .text {
  opacity: 1;
  bottom: 20px;
}
.loader.motion-out-1 .logo {
  transform: translateY(-20px);
  opacity: 0;
}
.loader.motion-out-1 .text {
  opacity: 0;
  bottom: -20px;
}
.loader.motion-out-2 .background {
  transform: translateY(-100%);
}
.loader .logo {
  width: 55px;
  height: 35px;
  position: relative;
  opacity: 0;
  transition: 1s all cubic-bezier(0.55, 0.085, 0, 0.99);
}
.loader .logo .svg {
  width: 100%;
  height: 100%;
  background: url('../img/logo-ico.svg') no-repeat center center;
  background-size: contain;
}
.loader .background {
  position: absolute;
  bottom: 0px;
  width: 100%;
  height: 100%;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
  overflow: hidden;
  background: #75D9F3;
  display: block;
  animation: gradient2 5s linear infinite;
}
.loader .background:before {
  content: '';
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 150rem;
  height: 150rem;
  border-radius: 100%;
  transform: translate(-50%, -50%) rotate(0deg) scale(1.4);
  background: linear-gradient(45deg, rgba(117, 217, 243, 0.7) 0%, rgba(117, 217, 243, 0) 50%), linear-gradient(225deg, rgba(251, 199, 89, 0.7) 0%, rgba(251, 199, 89, 0) 50%), linear-gradient(90deg, rgba(178, 126, 228, 0.7) 0%, rgba(178, 126, 228, 0) 50%), linear-gradient(135deg, rgba(45, 50, 148, 0.7) 0%, rgba(45, 50, 148, 0) 50%), linear-gradient(180deg, rgba(251, 103, 87, 0.7) 0%, rgba(251, 103, 87, 0) 50%);
  animation: gradient 30s linear infinite;
}
@keyframes gradient {
  0% {
    transform: translate(-50%, -50%) rotate(0deg) scale(1.4);
  }
  50% {
    transform: translate(-50%, -50%) rotate(180deg) scale(1.4);
  }
  100% {
    transform: translate(-50%, -50%) rotate(0deg) scale(1.4);
  }
}
@keyframes gradient2 {
  0% {
    background: #77B2AE;
  }
  25% {
    background: #FCD572;
  }
  50% {
    background: #ED8676;
  }
  75% {
    background: #FCD572;
  }
  100% {
    background: #77B2AE;
  }
}
.loader .text {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -20px;
  color: #fff;
  font-family: "Blacker Sans Pro Light";
  font-style: normal;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  font-size: 15px;
  opacity: 0;
  transition: 1s all cubic-bezier(0.55, 0.085, 0, 0.99);
  transition-delay: .4s;
}
