  position: fixed;
}
.home-infos .steps {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  z-index: 1;
}
.home-infos .steps.active {
  z-index: 2;
}
.home-infos .i-wrapper {
  width: 280px;
  right: 5%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.home-infos .button {
  margin-right: 0px;
  margin-left: auto;
  opacity: 0;
  transform: translateY(50px);
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
  transition-delay: .0s;
}
.home-infos .button.motion-in {
  opacity: 1;
  transition: 1.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
  transition-delay: .8s;
  transform: translateY(0px);
}
.home-infos .title {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 30%;
  left: 12%;
}
.home-infos .title .words {
  display: inline-block;
  position: relative;
  overflow: hidden;
}
.home-infos .title .words.w-block {
  display: block;
}
.home-infos .title .words.motion-in span {
  transform: translateY(0px);
}
.home-infos .title .words.bold {
  font-family: "Blacker Sans Pro Bold";
  font-style: normal;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
}
.home-infos .title .words span {
  display: block;
  transform: translateY(120px);
  transition: 1s all cubic-bezier(0.55, 0.085, 0, 0.99);
  margin-right: 10px;
  transition-delay: .5s;
  text-transform: uppercase;
}
.home-infos .paragraph {
  opacity: 0;
  transform: translateY(50px);
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
  transition-delay: 0s;
}
.home-infos .paragraph.motion-in {
  opacity: 1;
  transition: 1.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
  transition-delay: .5s;
  transform: translateY(0px);
}
@media (max-width: 850px) {
  .home-infos .paragraph {
    text-align: center;
  }
  .home-infos .steps {
    height: auto;
    width: 60%;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 20%;
  }
  .home-infos .title {
    text-align: center;
    position: relative;
    transform: none;
    left: auto;
    width: 100%;
    top: auto;
  }
  .home-infos .button {
    margin: 0 auto;
    margin-top: 40px;
  }
  .home-infos .i-wrapper {
    position: relative;
